
.footer{
    position: fixed;
    bottom: 0;
    background: #ddd;
    width: 100%;
}
*{
    margin: 0;
    padding: 0;
}
.footer ul{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.footer li{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    line-height: 50px;
}
a{
    text-decoration: none;
}
ul,li{
    list-style: none;
}
.add{
    background: #41b883;
    border: none;
    line-height: .8rem;
    color: #fff;
    margin: 0 auto;
    font-size: .4rem;
    width: 3rem;
}
.addPerson{
    border-radius: .1rem;
}
.info{
    text-align: center;
}
.newInput{
    margin: .2rem 2.5rem;
    border: 1px solid #000;
    display: inline-block;
    height: .8rem;
    width: 5rem;
    font-size: .4rem;
}
table{
    margin: .3rem auto;
    width: 85%;
    border-collapse: collapse;
}
table th,table td{
    border: 1px solid #ccc;
    font-size: .42rem;
    padding: .1rem;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}














































































































































